body {
  height: 100%;
  max-height: 100%;
  max-width: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
}

*{
  box-sizing: border-box;
}

.backgroundContainer{
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  border-spacing: 0;
}

.background {
  min-height: 100%;
  width: 100%;
  height: auto;
  position: fixed;
  object-fit: cover;
}

/*----------------------pagina LOGIN-----------------------*/
.box{
  width: 600px;
  height: 500px;
  background-color: rgba(2, 21, 50, 0.62);
  border-radius: 40px;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  position: relative;
  margin: auto;
  top: 350px;
}

.box img {
  max-width: 150px;
  max-height: 150px;
  width: auto;
  height: auto;
  margin-bottom: 30px;
  margin-left: 220px;
}

.login-form {
  height: 300px;
  max-width: 400px;
  opacity: 200%;
}

.caixaMail {
  border-radius: 100px;
  margin-left: 100px;
  margin-bottom: 10px;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
  height: 45px;
}

.forgot {
  color: #FFFFFF;
  margin-right: 50px;
  font-size: 16px;
}

.remember {
  color: white;
  display: block;
}

.register{
  color: #FFFFFF;
  margin-top: 20px;
  font-size: 16px;
}

.login-form-button {
  width: 100%;
  height: 65px;
  margin-left: 100px;
  background-color: #33FFAD;
  border-radius: 100px;
  color: #707070;
  font-size: 16px;
}

/*--------------------------pagina REGISTO---------------------*/
.title {
  color: #FFFFFF;
  font-size: 45px;
  margin-left: 100px;
}

.boxRegister {
  width: 600px;
  height: 1000px;
  background-color: rgba(2, 21, 50, 0.62);
  border-radius: 40px;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: left;
  position: relative;
  margin: auto;
  top: 150px;
}

.label {
  color: #FFFFFF;
  font-size: 20px;
  margin-left: 100px;
}

.textTermos {
  color: #FFFFFF;
  margin-left: 50px;
}

.login-form-button-register {
  width: 100%;
  height: 65px;
  background-color: #33FFAD;
  border-radius: 100px;
  color: #707070;
  margin-left: -50px;
  font-size: 16px;
}

.textColor {
  color: #FFFFFF;
  text-align: center;
  font-size: 18px;
}

.registerButton {
  color: #3fadec;
  font-size: 16px;
}

.boxRegister input {
  border-radius: 100px;
}
.registerPassword {
  border-radius: 100px;
}

/*--------------------------pagina FORRGOT---------------------*/
.box-forgot{
  width: 500px;
  height: 500px;
  background-color: rgba(2, 21, 50, 0.62);
  border-radius: 40px;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  position: relative;
  margin: auto;
  top: 350px;
}

.titleReset {
  color: #FFFFFF;
  font-size: 40px;
}

.inputMail {
  margin-top: 10px;
  border-radius: 100px;
  margin-left: 50px;
  height: 45px;
}

.login-form-button-reset {
  width: 100%;
  height: 65px;
  background-color: #33FFAD;
  border-radius: 100px;
  margin-left: 50px;
  margin-top: 20px;
  color: #707070;
  font-size: 16px;
}

.texto {
  text-align: justify;
  justify-content: center;
  color: #FFFFFF;
  font-size: 20px;
}

.text-center {
  margin-left: 100px;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  color: #FFFFFF;
  font-size: 16px;
}


.login-form-forgot {
  margin: auto;
}

.ant-col-rtl .login-form-forgot {
  float: left;
}

.site-form-item-icon {
  color: black;
}


/* ----------- iPad Pro ----------- */
/* Portrait and Landscape */
@media only screen 
  and (min-width: 1024px) 
  and (max-height: 1366px) { 
  /* and (-webkit-min-device-pixel-ratio: 1.5) */
    .login-form {
      height: 300px;
      width: 500px;
    }
    .login-form-forgot {
      float: right;
    }
    .ant-col-rtl .login-form-forgot {
      float: left;
    }
    .login-form-button {
      width: 100%;
    }
  }

/* ----------- Galaxy Fold ----------- */
/* Portrait and Landscape */
@media only screen 
  and (min-width: 280px) 
  and (max-height: 653px) 
  and (-webkit-min-device-pixel-ratio: 1.5) {
    .login-form {
      height: 400px;
      max-width: 250px;
    }
    .login-form-forgot {
      float: right;
    }
    .ant-col-rtl .login-form-forgot {
      float: left;
    }
    .login-form-button {
      width: 100%;
    }
    
    .register-form {
      width: 210px;
      height: 550px;
    }
  }

